Object.defineProperties(Array.prototype, {
    // 'Aggregate': {
    //     configurable: false,
    //     enumerable: false,
    //     writable: false,
    //     value: function(this: Array<any>, aggregator: (n: any, elem: any) => any){
    //         let result = '';
    //         for (let item of this){
    //             result = aggregator(result, item);
    //         }
    //         return result;
    //     }
    // },
    'Aggregate': {
        configurable: false,
        enumerable: false,
        writable: false,
        value: function (init, aggregator, resultSelector) {
            let result = init;
            for (let item of this) {
                result = aggregator(result, item);
            }
            return resultSelector ? resultSelector(result) : result;
        }
    },
    'All': {
        configurable: false,
        enumerable: false,
        writable: false,
        value: function (predicate) {
            for (let item of this) {
                if (!predicate(item))
                    return false;
            }
            return true;
        }
    },
    'Any': {
        configurable: false,
        enumerable: false,
        writable: false,
        value: function (predicate) {
            for (let item of this) {
                if (predicate(item))
                    return true;
            }
            return false;
        }
    },
    'Count': {
        configurable: false,
        enumerable: false,
        writable: false,
        value: function (predicate) {
            if (!predicate)
                return this.length;
            return this.filter(x => predicate(x)).length;
        }
    },
    'FirstOrDefault': {
        configurable: false,
        enumerable: false,
        writable: false,
        value: function (predicate) {
            console.log(this);
            for (let item of this) {
                console.log(item);
                console.log(predicate(item));
                if (predicate(item))
                    return item;
            }
            ;
            return undefined;
        }
    },
    'Select': {
        configurable: false,
        enumerable: false,
        writable: false,
        value: function (selector) {
            const result = [];
            this.forEach(x => result.push(selector(x)));
            return result;
        }
    },
    'Where': {
        configurable: false,
        enumerable: false,
        writable: false,
        value: function (predicate) {
            return this.filter(x => predicate(x));
        }
    },
});
export {};
